import { generateChangePercentagePrompt } from "@rp/react/common/watchlists"
import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function changePercentage(tableName: string) {
  const title = "Chg %"
  return genericColumn({
    endpoint: "companyQuote",
    columnKeys: ["changePercentage"],
    size: 90,
    title,
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          queryResult={info.row.original.financialData.companyQuote}
          tooltip={"Explain the price change"}
          onClick={() => {
            if (!info.row.original.financialData.companyQuote?.data) return
            const { prompt, displayedMessage } = generateChangePercentagePrompt(
              info.row.original.entityInfo.name,
              info.row.original.financialData.companyQuote?.data,
            )
            info.row.original.onChatWithCell?.({
              prompt,
              displayedMessage,
              value: info.row.original.financialData.companyQuote?.data?.changePercentage,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyQuote?.data?.changePercentage}
        />
      )
    },
  })
}

export { changePercentage }
